import React, { Component } from "react";
import "./style.css";
import Nav from "../components/Nav";
import Jumbotron from "../components/Jumbotron";
import goku from "../images/goku.jpg";
import cell from "../images/Cell.png";
import frieza from "../images/Frieza_Artwork.png";
import gohan from "../images/gohan.png";
import krillin from "../images/Krillin.jpg";
import vegeta from "../images/vegeta.jpg";
import yamcha from "../images/Yamcha.jpg";
import trunks from "../images/Trunks.png";
import piccolo from "../images/piccolo.jpg";
import { Row, Container } from "../components/Grid";
import { Shake } from 'reshake';

const Allimages = [
    {
        value: goku,
        name: "goku"
    },
    {
        value: cell,
        name: "cell"
    },
    {
        value: frieza,
        name: "frieza"
    },
    {
        value: gohan,
        name: "gohan"
    },
    {
        value: krillin,
        name: "krillin"
    },
    {
        value: vegeta,
        name: "vegeta"
    },
    {
        value: yamcha,
        name: "yamcha"
    },
    {
        value: trunks,
        name: "trunks"
    },
    {
        value: piccolo,
        name: "piccolo"
    }];


class Game extends Component {
    state = {
        Allimages: Allimages,
        Choices: Allimages,
        score: 0,
        topscore: 0,
        victory: "Lets start the game!",
        shouldwiggle: false
    };

    // images = [goku, cell, frieza, gohan, krillin, vegeta, yamcha, trunks];
    checkGuess = a => {
        const result = this.state.Choices.find(image => image.name === a);
        if (result) {
            let filter = this.state.Choices.filter(function (a) {
                return a !== result;
            })
            console.log(filter)
            let myscore = this.state.score + 1;
            if (myscore > this.state.topscore) {
                this.setState({
                    Choices: filter,
                    score: myscore,
                    topscore: myscore,
                    victory: "You guessed correctly!"
                })
            }
            else {
                this.setState({
                    Choices: filter,
                    score: myscore,
                    victory: "You guessed correctly!"
                })
            }
            this.shuffle(this.state.Allimages);
        }
        else {
            this.setState({
                victory: "You guessed incorrectly! Starting again in 2 seconds!",
                shouldwiggle: true
            })
            setTimeout(this.startgame, 2000)
        }

    }

    startgame = () => {
        this.setState({
            Choices: Allimages,
            score: 0,
            victory: "Lets start the game!",
            shouldwiggle: false
        })
        this.shuffle(this.state.Allimages);
    }
    shuffle = a => {
        for (let i = a.length - 1; i > 0; i--) {
            const j = Math.floor(Math.random() * (i + 1));
            [a[i], a[j]] = [a[j], a[i]];
        }
        this.setState({
            Allimages: a
        })
    };



    render() {
        return (
            <div>
                <Nav score={this.state.score} topscore={this.state.topscore} victory={this.state.victory} />
                <Jumbotron>
                    <div style={{ backgroundColor: "white", opacity: 0.8 }}>
                        <h1>Clicky Game</h1>
                        <h2>Click on an image to earn points, but don't click on any more than once!</h2>
                    </div>
                </Jumbotron>

                <Container>
                    <Row classAdd= {this.state.shouldwiggle?"wiggle":""}>
                        {this.state.Allimages.map(image => (
                            <div className="col-md-4 text-center" key={image.name}>
                                <div className="hover01">
                                    <figure><img src={image.value} alt={image.name} onClick={() => this.checkGuess(image.name)} /></figure>
                                </div>
                            </div>
                        ))}
                    </Row>
                </Container>

            </div>
        );
    }
}

export default Game;